body {
  margin: 0;
  overflow: hidden;
  background-color: #111; }
  body #loader {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); }
    body #loader .bar {
      width: 6px;
      height: 60px;
      background: #2980b9;
      display: inline-block;
      animation: loading 1.5s ease-in-out infinite; }
    body #loader .bar1 {
      animation-delay: 0.1s; }
    body #loader .bar2 {
      animation-delay: 0.2s; }
    body #loader .bar3 {
      animation-delay: 0.3s; }
    body #loader .bar4 {
      animation-delay: 0.4s; }
    body #loader .bar5 {
      animation-delay: 0.5s; }
    body #loader .bar6 {
      animation-delay: 0.6s; }
    body #loader .bar7 {
      animation-delay: 0.7s; }
    body #loader .bar8 {
      animation-delay: 0.8s; }

@keyframes loading {
  0% {
    transform: scaleY(1);
    background: #2980b9; }
  50% {
    transform: scaleY(0);
    background: transparent; }
  100% {
    transform: scaleY(1);
    background: #2980b9; } }
  body #gameArea {
    position: absolute;
    left: 50%;
    top: 50%;
    display: none; }
    body #gameArea #gameCanvas {
      width: 100%;
      height: 100%;
      background-image: url("https://raw.githubusercontent.com/marcin-brumer/Avoid-The-Spikes/master/dist/img/background.jpg");
      background-size: 100% 100%; }
    body #gameArea .menu {
      background-color: rgba(0, 0, 0, 0.8);
      position: absolute;
      left: 30%;
      top: 25%;
      width: 40%;
      height: 50%;
      border-radius: 10%;
      border: 1vh solid #fff;
      text-align: center;
      color: #fff; }
      body #gameArea .menu h1 {
        font-size: 7vh; }
      body #gameArea .menu p {
        font-size: 4vh; }
      body #gameArea .menu .button {
        position: absolute;
        left: 50%;
        top: 70%;
        transform: translate(-50%, -50%);
        padding: 1vh 4vh;
        font-size: 2.5vh;
        border-radius: 1vh;
        text-decoration: none;
        outline: none;
        color: #fff;
        background-color: #3498db;
        border: none;
        border-bottom: 0.6vh solid #2980b9;
        cursor: pointer; }
        body #gameArea .menu .button:active {
          transform: translate(-50%, -45%);
          border-bottom: none; }
    body #gameArea #game_over_menu {
      display: none; }
